@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/receptnik-croz/static/media/bg.601b1e15.png) repeat;
  background-size: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Inner{
    margin: 5vh 5vw;
}
#HeaderBox{
    height: auto;
}
#Title{
    font-family: 'Bangers', cursive;
    font-size: 50px;
}
#UserBoard{
    float: right;
}
#UserBoard > *{
    margin-left: 20px;
    margin-bottom: 10px;
    font-size: 25px;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}
@media screen and (max-width: 768px){
    #UserBoard{
        float: left;
        -webkit-transform: translate(-5%, -50%);
                transform: translate(-5%, -50%);
    }
    #UserBoard:first-child{
        margin-top: 20px;
    }
}


#ContentBox > *,
.RecipesBox > *{
    margin-top: 20px;
}

#Ikonice > *{
    border: 1px solid #303030;
    border-radius: 10%;
    margin-left: 20px;
    height: 48px;
    width: 48px;
    float: right;
    padding: 5px;
    color: #303030;
}
#Ikonice > *:hover{
    color: #fff;
    background-color: #303030;
    cursor: pointer;
}
#AddNewRecipe{
    height: 48px;
    width: 200px;
    color: #4bb543;
    border: 3px solid #4bb543;
}
#AddNewRecipe:hover{
    color: #fff;
    background-color: #4bb543;
    cursor: pointer;
}
#AddNewRecipe > *{
    font-size: 50px;
    padding: 5px;
}
.RecipeName{
    font-size: 20px;
}
.RecipeName:hover{
    cursor: pointer;
    box-shadow: 2px 2px rgba(0, 0, 0, 0.507);
}
#NotLoggedIn{
    text-align: center;
}
#NotLoggedIn h3{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: Arial, Helvetica, sans-serif;
}
.HeaderBox{
  background-color: #fdff8a;
}
